'use client'

import { Button } from '@/components/ui/button'
import { Play, Heart, Share2 } from 'lucide-react'
import { useState, useEffect } from 'react'
import { AnimatePresence, motion } from 'framer-motion'

interface AnimeActionButtonsProps {
  animeId: number
  trailerUrl?: string | null
}

interface ToastMessage {
  id: number
  text: string
}

export default function AnimeActionButtons({ animeId, trailerUrl }: AnimeActionButtonsProps) {
  const [shareStatus, setShareStatus] = useState<'idle' | 'copied'>('idle')
  const [isFavorite, setIsFavorite] = useState(false)
  const [toasts, setToasts] = useState<ToastMessage[]>([])

  // ===================== TOAST HANDLER =====================
  const showToast = (text: string) => {
    const id = Date.now()
    setToasts(prev => [...prev, { id, text }])
    setTimeout(() => setToasts(prev => prev.filter(t => t.id !== id)), 3000)
  }

  // ===================== FETCH STATUS =====================
  useEffect(() => {
    const fetchStatus = async () => {
      try {
        const res = await fetch(`/api/my-list/check?mal_id=${animeId}`)
        if (!res.ok) return
        const data = await res.json()
        setIsFavorite(data.exists)
      } catch (err) {
        console.error('Failed to fetch favorite status', err)
      }
    }
    fetchStatus()
  }, [animeId])

  // ===================== TOGGLE FAVORITE =====================
  const toggleFavorite = async () => {
    try {
      const endpoint = isFavorite ? '/api/my-list/remove' : '/api/my-list/add'
      const res = await fetch(endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ mal_id: animeId })
      })
      if (!res.ok) throw new Error('Failed to update My List')
      const data = await res.json()
      setIsFavorite(data.action === 'added')
      showToast(data.action === 'added' ? 'Added to My List' : 'Removed from My List')
    } catch (err) {
      console.error(err)
      showToast('Action failed')
    }
  }

  // ===================== SHARE =====================
  const handleShare = async () => {
    const currentUrl = window.location.href
    try {
      if (navigator.share) {
        await navigator.share({ title: 'Check out this Anime!', url: currentUrl })
        setShareStatus('idle')
      } else if (navigator.clipboard) {
        await navigator.clipboard.writeText(currentUrl)
        setShareStatus('copied')
        setTimeout(() => setShareStatus('idle'), 3000)
      } else {
        alert('Link copied: ' + currentUrl)
      }
    } catch (err) {
      console.error(err)
      setShareStatus('idle')
    }
  }

  const glassButtonStyle = `
    gap-2 border-input backdrop-blur-sm transition-colors shadow-sm
    text-foreground bg-background/60
    hover:bg-primary hover:text-primary-foreground
    dark:hover:bg-white/20 dark:hover:text-white
  `

  const shareButtonClasses = shareStatus === 'copied'
    ? 'bg-green-500 text-white border-green-500 hover:bg-green-600'
    : glassButtonStyle

  return (
    <>
      {/* TOAST CONTAINER */}
      <div className="fixed top-20 right-6 z-50 flex flex-col gap-2 max-w-xs">
        <AnimatePresence>
          {toasts.map(t => (
            <motion.div
              key={t.id}
              initial={{ opacity: 0, x: 50 }}
              animate={{ opacity: 1, x: 0 }}
              exit={{ opacity: 0, x: 50 }}
              className="px-4 py-3 rounded-lg shadow-lg border border-border text-sm font-medium"
              style={{
                background: 'var(--card)',
                color: 'var(--card-foreground)',
              }}
            >
              {t.text}
            </motion.div>
          ))}
        </AnimatePresence>
      </div>

      <div className="flex flex-wrap gap-3 mt-4 justify-center md:justify-start">

        {trailerUrl ? (
          <Button className="gap-2 bg-primary text-primary-foreground hover:bg-primary/90 shadow-md" onClick={() => window.open(trailerUrl, '_blank')}>
            <Play size={18} /> Watch Trailer
          </Button>
        ) : (
          <Button disabled className="gap-2 bg-muted text-muted-foreground cursor-not-allowed opacity-70">
            <Play size={18} /> No Trailer
          </Button>
        )}

        <Button variant="outline" className={glassButtonStyle} onClick={toggleFavorite}>
          <Heart size={18} /> {isFavorite ? 'Remove from My List' : 'Add to My List'}
        </Button>

        <Button variant="outline" className={shareButtonClasses} onClick={handleShare} disabled={shareStatus === 'copied'}>
          <Share2 size={18} /> {shareStatus === 'copied' ? 'Copied!' : 'Share'}
        </Button>
      </div>
    </>
  )
}
