'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Search, Menu, X, Loader2, User, LogIn, LogOut, Settings, LayoutDashboard } from 'lucide-react' 
import { ThemeToggle } from './theme-toggle'
import { searchAnime, Anime } from '@/lib/api'

// ====================
// KOMPONEN: USER DROPDOWN (Hanya untuk Desktop)
// ====================
interface UserDropdownProps {
  onLogout: () => void
  onNavigate: () => void
}

const UserDropdown = ({ onLogout, onNavigate }: UserDropdownProps) => (
  <div className="absolute right-0 top-full mt-2 w-48 bg-card border border-border rounded-xl shadow-lg overflow-hidden z-50 animate-in slide-in-from-top-2">
    <Link 
      href="/dashboard" 
      onClick={onNavigate}
      className="flex items-center gap-2 px-4 py-2 hover:bg-primary/10 transition-colors"
    >
      <LayoutDashboard size={18} />
      <span>Dashboard</span>
    </Link>
    <Link 
      href="/settings" 
      onClick={onNavigate}
      className="flex items-center gap-2 px-4 py-2 hover:bg-primary/10 transition-colors"
    >
      <Settings size={18} />
      <span>Settings</span>
    </Link>
    <button 
      onClick={onLogout} 
      className="flex items-center gap-2 w-full text-left px-4 py-2 text-destructive hover:bg-destructive/10 transition-colors border-t border-border/50"
    >
      <LogOut size={18} />
      <span>Logout</span>
    </button>
  </div>
)

// ====================
// KOMPONEN UTAMA: NAVBAR
// ====================
export function Navbar() {
  const router = useRouter()
  // State Navigasi & Pencarian
  const [isOpen, setIsOpen] = useState(false) 
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)
  const [dropdownOpen, setDropdownOpen] = useState(false)
  
  // State Autentikasi
  const [isLoggedIn, setIsLoggedIn] = useState(true) 
  const [isUserDropdownOpen, setIsUserDropdownOpen] = useState(false)
  
  // Refs
  const abortControllerRef = useRef<AbortController | null>(null)
  const dropdownRef = useRef<HTMLDivElement>(null)

  const closeAllMenus = () => {
    setIsOpen(false)
    setMobileSearchOpen(false)
    setDropdownOpen(false)
    setIsUserDropdownOpen(false)
  }

  const handleLogin = () => {
    setIsLoggedIn(true)
    closeAllMenus()
  }

  const handleLogout = () => {
    setIsLoggedIn(false)
    closeAllMenus()
    router.push('/')
  }

  // Logika Pencarian
  useEffect(() => {
    if (!searchQuery.trim()) {
      setResults([])
      setLoading(false)
      setDropdownOpen(false)
      return
    }

    if (abortControllerRef.current) abortControllerRef.current.abort()
    const controller = new AbortController()
    abortControllerRef.current = controller
    setLoading(true)

    const timeoutId = setTimeout(async () => {
      try {
        const res = await searchAnime(searchQuery, 1, controller.signal)
        if (!controller.signal.aborted) {
          setResults(res.data?.slice(0, 6) || [])
          setLoading(false)
          setDropdownOpen(true)
        }
      } catch (err: any) {
        if (err.name !== 'AbortError') {
          setResults([])
          setLoading(false)
          setDropdownOpen(false)
        }
      }
    }, 500)

    return () => {
      clearTimeout(timeoutId)
      controller.abort()
    }
  }, [searchQuery])

  // Tutup dropdown saat klik luar
  useEffect(() => {
    const listener = (e: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target as Node)) {
        setDropdownOpen(false)
      }
      if (isUserDropdownOpen && !(e.target as HTMLElement).closest('.user-dropdown-area')) {
        setIsUserDropdownOpen(false)
      }
    }
    document.addEventListener('mousedown', listener)
    return () => document.removeEventListener('mousedown', listener)
  }, [isUserDropdownOpen])

  const handleSubmitSearch = (e?: React.FormEvent) => {
    e?.preventDefault()
    if (!searchQuery) return
    closeAllMenus()
    router.push(`/search?q=${encodeURIComponent(searchQuery)}`)
  }

  return (
    <>
      <nav className="sticky top-0 z-50 bg-card/95 backdrop-blur border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">

            {/* Logo */}
            <Link href="/" className="flex items-center gap-2 group">
              <div className="w-8 h-8 rounded-full overflow-hidden flex items-center justify-center">
                <img src="/feinime.jpg" alt="Logo Feinime" className="w-full h-full object-cover" />
              </div>
              <span className="text-xl font-bold text-foreground hidden sm:inline group-hover:text-primary transition-colors">
                Feinime
              </span>
            </Link>

            {/* Menu Navigasi Desktop */}
            <div className="hidden md:flex flex-1 justify-center items-center gap-6">
              {[
                { href: "/", label: "Home" },
                { href: "/trending", label: "Trending" },
                { href: "/seasonal", label: "Seasonal" },
                { href: "/popular", label: "Popular" },
                { href: "/genres", label: "Genres" },
              ].map((item) => (
                <Link key={item.href} href={item.href} className="text-foreground text-sm font-medium hover:text-primary hover:bg-primary/10 px-2 py-1 rounded-md transition-colors">
                  {item.label}
                </Link>
              ))}
              
              {isLoggedIn && (
                 <Link href="/my-list" className="text-foreground text-sm font-medium hover:text-primary hover:bg-primary/10 px-2 py-1 rounded-md transition-colors">
                  <span className="flex items-center gap-1">My List</span>
                </Link>
              )}
            </div>

            {/* Aksi Desktop: Search + Theme + Auth */}
            <div className="hidden md:flex items-center gap-2 relative">
              
              <form onSubmit={handleSubmitSearch} className="w-64 relative mr-2">
                <div className="flex items-center gap-2 bg-input border border-border rounded-lg px-3 py-2 w-full focus-within:ring-1 focus-within:ring-primary transition-all">
                  <Search size={18} className="text-muted-foreground" />
                  <input
                    type="text"
                    placeholder="Search anime..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="bg-transparent outline-none text-sm w-full placeholder:text-muted-foreground"
                    onFocus={() => searchQuery.trim() && setDropdownOpen(true)}
                  />
                  {loading && <Loader2 size={16} className="animate-spin text-primary" />}
                </div>

                {/* Dropdown Search Desktop */}
                {dropdownOpen && results.length > 0 && (
                  <div ref={dropdownRef} className="absolute top-full right-0 mt-2 w-64 bg-card border border-border rounded-xl shadow-lg overflow-hidden z-50 animate-in slide-in-from-top-2">
                    {results.map((anime) => (
                      <Link key={anime.mal_id} href={`/anime/${anime.mal_id}`} onClick={closeAllMenus} className="flex items-center gap-3 px-3 py-2 hover:bg-primary/10 border-b border-border/50 last:border-0 transition-colors">
                        <img src={anime.images.jpg.image_url || anime.images.jpg.large_image_url} alt={anime.title} className="w-10 h-14 object-cover rounded" />
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-medium truncate">{anime.title}</p>
                          <p className="text-xs text-muted-foreground">{anime.type} • {anime.episodes ?? "?"} eps</p>
                        </div>
                      </Link>
                    ))}
                    <button onClick={() => handleSubmitSearch()} className="w-full text-center text-sm font-semibold text-primary bg-secondary/30 py-3 hover:bg-secondary/50 transition-colors rounded-b-xl">
                      View all results
                    </button>
                  </div>
                )}
              </form>

              <ThemeToggle />

              {/* Login/User Button Desktop - STYLE DIUBAH MENJADI GHOST (SEPERTI TOGGLE) */}
              <div className="relative user-dropdown-area">
                {isLoggedIn ? (
                  <>
                    <button 
                      // UBAH DISINI: Gunakan 'hover:bg-secondary' dan 'rounded-lg' (bukan full circle)
                      // agar sama persis dengan ThemeToggle
                      className="p-2 rounded-lg hover:bg-secondary transition-colors text-foreground"
                      title="User Menu"
                      onClick={() => setIsUserDropdownOpen(!isUserDropdownOpen)}
                    >
                      <User size={20} />
                    </button>
                    {isUserDropdownOpen && (
                      <UserDropdown onLogout={handleLogout} onNavigate={closeAllMenus} />
                    )}
                  </>
                ) : (
                  <button onClick={handleLogin} className="flex items-center gap-1 text-foreground text-sm font-medium hover:text-primary hover:bg-primary/10 px-3 py-2 rounded-md transition-colors">
                    <LogIn size={18} /> Login
                  </button>
                )}
              </div>
            </div>

            {/* Aksi Mobile: Search + Theme + Auth + Menu Toggle */}
            <div className="flex md:hidden items-center gap-2">
              <button className="p-2 hover:bg-secondary rounded-lg transition-colors" onClick={() => setMobileSearchOpen(true)}>
                <Search size={24} />
              </button>
              
              <ThemeToggle />
              
              {/* Login/User Button Mobile - STYLE GHOST */}
              {isLoggedIn ? (
                <Link 
                  href="/dashboard" 
                  className="p-2 hover:bg-secondary rounded-lg transition-colors text-foreground" 
                  onClick={closeAllMenus}
                >
                    <User size={24} />
                </Link>
              ) : (
                <button onClick={handleLogin} className="p-2 hover:bg-secondary rounded-lg transition-colors">
                  <LogIn size={24} />
                </button>
              )}

              <button className="p-2 hover:bg-secondary rounded-lg transition-colors" onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <X size={24} /> : <Menu size={24} />}
              </button>
            </div>
          </div>

          {/* Menu Navigasi Mobile */}
          {isOpen && (
            <div className="md:hidden mt-2 space-y-2 px-2 pb-4 animate-in slide-in-from-top-5">
              {[
                { href: "/", label: "Home" },
                { href: "/trending", label: "Trending" },
                { href: "/seasonal", label: "Seasonal" },
                { href: "/popular", label: "Popular" },
                { href: "/genres", label: "Genres" },
              ].map((item) => (
                <Link key={item.href} href={item.href} className="block px-3 py-2 rounded-md hover:bg-primary/20" onClick={closeAllMenus}>{item.label}</Link>
              ))}

              {isLoggedIn && (
                <>
                  <div className="h-px bg-border my-2" />
                  <Link href="/dashboard/favorites" className="block px-3 py-2 rounded-md hover:bg-primary/20 text-foreground" onClick={closeAllMenus}>
                    My List
                  </Link>
                  <Link href="/dashboard" className="block px-3 py-2 rounded-md hover:bg-primary/20 text-foreground" onClick={closeAllMenus}>
                    <LayoutDashboard size={18} className="inline mr-2 text-primary" /> Dashboard
                  </Link>
                  <Link href="/settings" className="block px-3 py-2 rounded-md hover:bg-primary/20 text-foreground" onClick={closeAllMenus}>
                    <Settings size={18} className="inline mr-2 text-primary" /> Settings
                  </Link>
                  <button onClick={handleLogout} className="w-full text-left block px-3 py-2 rounded-md text-destructive hover:bg-destructive/10">
                    <LogOut size={18} className="inline mr-2" /> Logout
                  </button>
                </>
              )}
            </div>
          )}
        </div>
      </nav>

      {/* Overlay Pencarian Mobile */}
      {mobileSearchOpen && (
        <div className="fixed inset-0 bg-background/95 backdrop-blur-lg z-[999] p-4 animate-in fade-in duration-200">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold">Search Anime</h2>
            <button onClick={() => { setMobileSearchOpen(false); setResults([]); setSearchQuery('') }} className="p-2 rounded-lg hover:bg-secondary">
              <X size={26} />
            </button>
          </div>

          <div className="flex items-center gap-3 bg-input border border-border rounded-xl px-4 py-3 focus-within:ring-1 focus-within:ring-primary transition-all">
            <Search size={20} className="text-muted-foreground" />
            <form onSubmit={handleSubmitSearch} className="flex-1">
              <input autoFocus type="text" placeholder="Search anime..." value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} className="bg-transparent outline-none text-base w-full" />
            </form>
            {loading && <Loader2 className="animate-spin text-primary" size={20} />}
          </div>

          <div className="mt-4 space-y-2 max-h-[70vh] overflow-y-auto">
            {results.length === 0 && searchQuery && !loading && (
              <p className="text-center text-muted-foreground mt-10">No results found.</p>
            )}
            {results.map((anime) => (
              <Link key={anime.mal_id} href={`/anime/${anime.mal_id}`} onClick={closeAllMenus} className="flex items-center gap-3 p-2 rounded-lg hover:bg-primary/10 transition animate-in slide-in-from-bottom-2">
                <img src={anime.images.jpg.image_url || anime.images.jpg.large_image_url} alt={anime.title} className="w-12 h-16 object-cover rounded" />
                <div>
                  <p className="font-medium line-clamp-1">{anime.title}</p>
                  <p className="text-xs text-muted-foreground">{anime.type} • {anime.episodes ?? "?"} eps</p>
                </div>
              </Link>
            ))}
            {results.length > 0 && (
              <button onClick={() => handleSubmitSearch()} className="w-full py-3 mt-2 text-center text-primary font-bold border border-primary/20 rounded-lg hover:bg-primary/10">
                See all results
              </button>
            )}
          </div>
        </div>
      )}
    </>
  )
}